









































































import Vue from 'vue'
import {mapGetters, mapActions} from 'vuex'
import dict from '@custom/dict'
import utils from '@corets/utils'

const NLangPicker = () => import('@corecp/NLangPicker.vue');

export default Vue.extend({
    name: 'NHeader',
    computed: {
        ...mapGetters([
            'comConfig',
            'menuObj',
            'curMenu',
            'userInfo',
            'isFullScreen'
        ])
    },
    components: {
        NLangPicker
    },
    data () {
        return {
            dict
        }
    },
    methods: {
        ...mapActions([
            'changeCurMenu',
            'changeFullScreen'
        ]),
        changeMenu ({key}: { key: any }) {
            const self = this as any
            self.changeCurMenu([key])
        },
        toLogline () {
            const self = this as any
            self.$newpage({
                title: dict.localeObj.personalCenter.frontendLog,
                component: dict.commonObj.loglinePath
            })
        },
        toSetting () {
            const self = this as any
            self.$newpage({
                title: dict.localeObj.personalCenter.customSetting,
                component: dict.commonObj.settingPath
            })
        },
        setFullScreen() {
            const self = this as any
            utils.fullScreenCtl(!self.isFullScreen)
        },
        toClean () {
            const self = this as any
            dict.commonObj.localStorageKey.forEach((item) => {
                localStorage.removeItem(item)
            })
            self.$message.success(
                self.$t(dict.localeObj.personalCenter.errorTip.cleanSuccess) as string,
                2
            ).then(() => {
                window.location.reload()
            })
        },
        toCacheClean () {
            const self = this as any
            // need to clean cache storage
            for (const key of Object.keys(localStorage)) {
                if (/near_cache/.test(key)) {
                    localStorage.removeItem(key)
                }
            }
            self.$message.success(
                self.$t(dict.localeObj.personalCenter.errorTip.cleanSuccess) as string,
                2
            ).then(() => {
                window.location.reload()
            })
        }
    }
});
